import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	AdaptivityProvider,
	//   ConfigProvider,
	//   useAdaptivity,
	AppRoot,
	SplitLayout,
	SplitCol,
	//ViewWidth,
	View,
	Panel,
	PanelHeader,
	Header,
	Group,
	Button,
	//SimpleCell,
	Div,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";




bridge
	.send("VKWebAppFlashSetLevel", { "level": 1 })
	.then(data => {
		// Обработка события в случае успеха
		console.log(data.result)
	})
	.catch(error => {
		console.log("Мне так плёха(")
		// Обработка события в случае ошибки
	});

const App = () => {
	//const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);


	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
		}
		fetchData();
	}, []);



	return (
		<AdaptivityProvider>
			<AppRoot>
				<SplitLayout header={<PanelHeader separator={false} />}>
					<SplitCol>
						<View activePanel="main">
							<Panel id="main">
								<PanelHeader>Наша команда</PanelHeader>
								<Group header={<Header mode="secondary">Растягивание по ширине</Header>}>
									<Div style={{ display: 'flex' }}>
										<Button size="s" stretched mode="primary"></Button>
										<Button size="s" stretched mode="primary"></Button>
										<Button size="s" stretched mode="primary"></Button>
										<Button size="s" stretched mode="primary"></Button>
										<Button size="s" stretched mode="primary"></Button>
										<Button size="s" stretched mode="primary"></Button>
										<Button size="s" stretched mode="primary"></Button>
										<Button size="s" stretched mode="primary"></Button>
									</Div>
								</Group>
							</Panel>
						</View>
					</SplitCol>
				</SplitLayout>
			</AppRoot >
		</AdaptivityProvider >
	);
}

export default App;
